@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.icon-image {
    width: 50px;
    height: 50px;
    padding: 0 !important;
  }
  
  .reaction {
    padding-right: 30px !important;
  }
  
  .outer-post {
    border-bottom: 0.5px lightgrey solid;
    padding: 15px 20px !important;
  }
  
  .posted-image {
    max-height: 200px;
    max-width: 200px;
    margin-bottom: 10px;
    border-radius: 20px;
  }
  
  .time {
    color: #8c8c8c;
    font-size: 12px;
  }
  
  .reactions {
    padding: 5px 0;
  }
  
  .message {
    padding: 10px 0px;
  }
  
  .name {
    padding: 0 0 0 10px !important;
  }
  
  .posted-image {
    padding: 0 0 10px 0 !important;
  }
  
  .reaction {
    vertical-align: middle;
  }
  
  .seeMoreButton {
    background-color: #ED5481;
    color: white;
    font-weight: 500;
    text-align: center;
    width: 250px;
    border-radius: 50px;
    padding: 5px;
    margin: auto;
  }

  .seeMoreContainer {
    font-size: 16px;
  }
  
  /* Black Overlay */
  
  .black {
    background-color: black;
    z-index: 2;
    height: 100%;
    width: 100%;
    position: fixed;
    opacity: .5;
    top: 0;
    bottom: 0;
  }
  
  /* Overlay Card */
  
  .overlayCard {
    z-index: 3;
    position: fixed;
    width: 100%;
    height: 450px;
    top: calc(100% - 450px);
    background-color: #F1F1F1;
    border-radius: 20px 20px 0 0;
  }
  
  .page-header {height: 52px;}
  .lightbulb {height: 80px;}
  .text-container {height: 217px;}
  .text-container-2 {height: 120px;}
  .text-container-3 {height: 297px;}
  textarea {height: 177px;}
  
  .close-button {
    position: fixed;
    left: 85vw;
    top: calc(100% - 430px);
    z-index: 5;
  }
  
  .big-text {
    font-size: 25px;
    padding-bottom: 10px;
  }
  
  .page {
    padding: 25px 30px !important;
  }
  
  .bold {font-weight: normal;}
  
  .bold-pink {
    font-weight: normal;
    color: #ED5481;
  }
  
  .main-form {
    width: 100%;
  }
  
  textarea {
    width: 100%;
    text-align: left;
    vertical-align: bottom;
    padding: 15px;
    font-weight: 300;
  }
  
  .title {padding-bottom: 15px !important;}
  
  #audio {
    width: 100%;
    max-width: 290px;
    border-radius: 0;
  }
  
  .count {
    color: #8c8c8c;
    font-size: 12px;
  }
  
  .post-poster {
    padding: 0 0 5px 0;
  }
  
  .name {
   padding: 0 0 0 5px;
   letter-spacing: 0px;
   font-weight: normal !important;
  }
  
  #close {
    text-decoration: none;
    cursor: pointer;
  }
  
  .no-underline, .no-underline:hover, .no-underline:active, .no-underline:visited, .no-underline:focus {
      text-decoration: none !important;
  }
  
  #button-holder {
    margin-top: 15px !important;
  }
